import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from './components/Modal';

import { fetchItems } from './redux/actions/items';

import './style.css';

function App() {
  const dispatch = useDispatch();

  const [selectedItem, setSelectedItem] = useState(null);

  const [searchItem, setSeaarchItem] = useState('');

  const items = useSelector(({ items }) => items.items);

  const inputHandler = (e) => {
    setSeaarchItem(e.target.value);
  };

  const submitHandler = (e) => {
    dispatch(fetchItems(searchItem));
  };

  const submitKeyHandler = (e) => {
    if (e.key === 'Enter') {
      dispatch(fetchItems(searchItem));
    }
  };

  return (
    <div className="app">
      <div className="input">
        <input
          type="text"
          onChange={inputHandler}
          onKeyPress={submitKeyHandler}
        />
        <button onClick={submitHandler}>Search</button>
      </div>
      {items.map((item, index) => (
        <div
          className="image-container"
          key={item.id}
          id={index}
          onClick={() => setSelectedItem(item)}
        >
          <img className="image" src={item.urls.regular} alt="logo" />
          <div className="info">
            <img
              className="avatar"
              src={item.user.profile_image.small}
              alt="avatar"
            />
            <div className="text">
              <p className="name">{item.user.username}</p>
              <p className="name">{item.alt_description}</p>
            </div>
          </div>
        </div>
      ))}
      {selectedItem && (
        <Modal
          active={true}
          onClose={() => setSelectedItem(null)}
          url={selectedItem.urls.regular}
        />
      )}
    </div>
  );
}
export default App;
